import React, { useState, useEffect, Suspense } from "react";
import "./App.css";
import { BrowserRouter as Router, Redirect } from "react-router-dom";
import { createBrowserHistory } from "history";
import Homepage from "./components/homepage/HomePage";
import Navbar from "./components/navbar/Navbar";
import SideBar from "./components/sidebar/SideBar.js";
import SwitchRoute from "./components/switchRoute/SwitchRoute";
import Dotenv from "dotenv";
import { TextField, Button, Typography } from "@material-ui/core";
import ReactLoading from "react-loading";
import HomePage from "./components/homepage/HomePage";

function App() {
  const [menuDefault, setMenu] = useState(false);
  const [imageUrl, setImageUrl] = useState("");
  const [disableDiagnoseBtn, setDisableDiagnoseBtn] = useState(true);
  const [confidenceState, setConfidenceState] = useState(null);
  const [diagnosisState, setDiagnosisState] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [analysisPageLoadingState, setAnalysisPageLoadingPage] = useState(
    false
  );
  const [homePageLoadingState, setHomePageLoadingSate] = useState(true);
  const [openFailedRequestState, setOpenFailedRequestState] = useState(false);

  useEffect(() => {
    setHomePageLoadingSate(false);
  });

  const onMenuClick = () => {
    setMenu(true);
  };

  const history = createBrowserHistory();

  const imageUrlHandler = (e) => {
    setImageUrl("");
    setDiagnosisState(null);
    setConfidenceState(null);
    setDisableDiagnoseBtn(true);
    let dataInput = e.target.value;
    let regexp = /(ftp|http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/;
    if (regexp.test(dataInput)) {
      setImageUrl(e.target.value);
      setDisableDiagnoseBtn(false);
    }
  };

  const handleFailedRequestState = () => {
    console.log("handle failed request state");
    setOpenFailedRequestState(false);
  };

  const handleDetect = () => {
    setIsLoading(true);
    setDisableDiagnoseBtn(true);
    setConfidenceState(null);
    setDiagnosisState(null);
    const regexp = /(ftp|http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/;
    if (imageUrl === "" && !regexp.test("imageUrl")) {
      // do nothing
    } else {
      // alert(imageUrl);
      const params = {
        // Request parameters
        application: "{string}",
      };
      const url = `https://southcentralus.api.cognitive.microsoft.com/customvision/v3.0/Prediction/689d053d-6d91-4a9f-92df-11ab7cf50cd7/classify/iterations/Iteration7/url`;
      const data = `{"url": "${imageUrl}"}`;
      // alert(JSON.stringify(data));
      fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Prediction-key": "895b417fa39b41368644572e9c7a9159",
        },
        body: data,
      })
        .then((response) => response.json())
        .then((result) => {
          console.log(result);
          let diagnosis = result.predictions[0].tagName;
          let confidence = result.predictions[0].probability * 100 + "%";
          setConfidenceState(confidence);
          setDiagnosisState(diagnosis);
          console.log("diagnosis: ", diagnosis);
          console.log("confidence: ", confidence);
          setIsLoading(false);
          setDisableDiagnoseBtn(false);
        })
        .catch((error) => {
          setIsLoading(false);
          setDisableDiagnoseBtn(false);
          setOpenFailedRequestState(true);
          console.log("Error:", error);
        });
    }
  };

  const closeMenu = () => {
    setMenu(false);
  };
  const handleSideBarOnOpen = (e) => {
    console.log(e);
  };

  return (
    <Suspense
      fallback={setTimeout(() => {
        console.log("loading...");
      }, 7000)}
    >
      {/* <Splash
        text="Project Name"
        src="https://source.unsplash.com/FxU8KV7psMY/1600x900"
        style={{ height: "70vh" }}
      /> */}
      <Router history={history} basename="/covidapp">
        <Navbar onMenuClick={onMenuClick} />
        <div
          style={{
            display: "flex",
            // backgroundColor: "#f7f7f7",
            backgroundColor: "white",
            height: "90vh",
            zIndex: "10",
          }}
        >
          <Redirect to="/" />
          <SwitchRoute
            imageUrlHandler={imageUrlHandler}
            handleDetect={handleDetect}
            imageUrl={imageUrl}
            disableDiagnoseBtn={disableDiagnoseBtn}
            diagnosisState={diagnosisState}
            confidenceState={confidenceState}
            isLoading={isLoading}
            analysisPageLoadingState={analysisPageLoadingState}
            openFailedRequestState={openFailedRequestState}
            handleFailedRequestState={handleFailedRequestState}
          />
          <SideBar
            menuDefault={menuDefault}
            closeMenu={closeMenu}
            handleSideBarOnOpen={handleSideBarOnOpen}
            handleSideBarOnClose={handleSideBarOnOpen}
          />
        </div>
      </Router>
    </Suspense>
  );
}

export default App;
