import React from "react";
import { Typography } from "@material-ui/core";

const AboutUsPage = () => {
  return (
    <div style={{ padding: "3%" }}>
      <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
        <Typography variant="h4" color="primary">
          About Us
        </Typography>
      </div>
      <div style={{ padding: "1%" }}>
        <Typography style={{ textAlign: "justify", color: "#696969" }}>
          KLOP is designed to tackle and render three major services which are
          imaging, diagnosis, and result interpretation which is indicated as
          percentage confidence feature. KLOP is built solely on Deeplearning
          Learning technology which enable it perform the listed services
          descretely with high accuracy. A major advantage of KLOP is its
          ability to create accurate and preecise result within an extremely
          short time frame which in turn helps to save time and reduce cost of
          image analysis and interpretation. With the evolution of our prototype
          KLOP a better imaging process is guaranteed
        </Typography>
      </div>
    </div>
  );
};

export default AboutUsPage;
